<template>
    <div class="main">
      <BackTop></BackTop>
      <Layout class="layout">
        <Header>
          <Menu mode="horizontal" :theme="theme" active-name="1" @on-select="menuSelect">
            <MenuItem name="1">
              <Icon type="ios-add-circle" />
              添加内容
            </MenuItem>
            <MenuItem name="2">
              <Icon type="ios-add-circle" />
              管理内容
            </MenuItem>
            <!--<MenuItem name="2">
              <Icon type="ios-people" />
              用户管理
            </MenuItem>
            <Submenu name="3">
              <template slot="title">
                <Icon type="ios-stats" />
                统计分析
              </template>
              <MenuGroup title="使用">
                <MenuItem name="3-1">新增和启动</MenuItem>
                <MenuItem name="3-2">活跃分析</MenuItem>
                <MenuItem name="3-3">时段分析</MenuItem>
              </MenuGroup>
              <MenuGroup title="留存">
                <MenuItem name="3-4">用户留存</MenuItem>
                <MenuItem name="3-5">流失用户</MenuItem>
              </MenuGroup>
            </Submenu>
            <MenuItem name="4">
              <Icon type="ios-construct" />
              综合设置
            </MenuItem>-->
          </Menu>
        </Header>
        <Content>
          <transition name="moveR" mode="out-in">
            <router-view name="content"/>
          </transition>
        </Content>
      </Layout>
    </div>
</template>

<script>
    export default {
      name: "Main",
      data () {
        return {
          theme: 'dark',
          path:['/addQuestion', '/questionAdmin'],
        }
      },
      methods:{
        menuSelect(name){
          this.$router.push(this.path[parseInt(name) - 1])
        }
      }
    }
</script>

<style scoped>
  .main{
    width: 80%;
    min-width: 1024px;
    margin: 0 auto;
  }
  .layout{
    min-height: 100vh;
  }
</style>



