import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import Main from '@/components/Main'
import AddQuestion from '@/components/pages/AddQuestion'
import QuestionAdmin from '@/components/pages/QuestionAdmin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome,
    },
    {
      path: '/addQuestion',
      name: 'Main',
      component: Main,
      children:[{
        path:'/',
        name:'addQuestion',
        components:{
          content:AddQuestion
        }
      }]
    },
    {
      path: '/questionAdmin',
      name: 'Main',
      component: Main,
      children:[{
        path:'/',
        name:'questionAdmin',
        components:{
          content:QuestionAdmin
        }
      }]
    },
  ]
})



